		}
	},
	methods: {
		dataFormat(time){
		   return `${time.getFullYear()}-${time.getMonth() + 1 >= 10 ? (time.getMonth() + 1) : '0' + (time.getMonth() + 1)}-${time.getDate() >= 10 ? time.getDate() : '0' + time.getDate()}`;
	    }
	},
	mounted() {
		this.data = this.dataFormat(new Date())
	}
}
</script>

