<template>
	<div id="fullpage">
		<full-page ref="fullpage" :options="options" id="fullpage">
			<Home />
			<Two />
			<Specialty />
			<Jingl />
			<Zuop />
			<Footer />
		</full-page>
	</div>
</template>

<script>
import Home from '@/views/home/index.vue';
import Two from '@/views/two/index.vue';
import Specialty from '@/views/specialty/index.vue';
import Jingl from '@/views/jingl/index.vue';
import Zuop from '@/views/zuop/index.vue';
import Footer from '@/views/footer/index.vue';
export default {
	name: 'fullpage',
	data() {
		return {
			options: {
				anchors: ['page1', 'page2', 'page3', 'page4', 'page5',  'page6'],
				fixedElements: '.nav',
				autoScrolling: true
			}
		};
	},
	components: {
		Home,
		Two,
		Specialty,
		Jingl,
		Zuop,
		Footer
	},
	methods: {
		// 获取屏幕宽度改变状态
		getScreen() {
			let clientWidth = document.documentElement.clientWidth || document.body.clientWidth;
			if (clientWidth < 768) {
				//当屏幕小于768时，设置Vuex里的数据
				this.$store.commit('SET_MOBLIE', true); // 是否是移动端
				this.$store.commit('SET_SCROLLTITLE', '向下滑动');
				this.$refs.fullpage.api.setAutoScrolling(false);
			} else {
				//反之，设置Vuex里的数据
				this.$store.commit('SET_MOBLIE', false);
				this.$store.commit('SET_SCROLLTITLE', '向下滚动');
				this.$refs.fullpage.api.setAutoScrolling(true);
			}
		},
		// 监听屏幕
		setMobile() {
			addEventListener('resize', () => {
				this.getScreen();
			});
		}
	},
	mounted() {
		this.getScreen();
		this.setMobile();
	}
};
</script>

<style lang="less">
* {
	padding: 0;
	margin: 0;
}
body,
html {
	width: 100%;
	height: 100%;
	a {
		color: #7e8c8d;
		text-decoration: none;
		-webkit-backface-visibility: hidden;
	}
	li {
		list-style: none;
	}
}
::-webkit-scrollbar {
	width: 5px;
}
::-webkit-scrollbar-track {
	border-radius: 10px;
	background-color: rgba(0, 0, 0, 0.1);
}
::-webkit-scrollbar-thumb {
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: rgba(0, 0, 0, 0.1);
}
</style>
