import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
	  isMobile:false, // 屏幕是否小于768
	  scrollTitle: "向下滚动"
  },
  mutations: {
	  // 判断是不是手机端
	  SET_MOBLIE(state,isMobile){
		  state.isMobile = isMobile;
	  },
	  SET_SCROLLTITLE(state, scrollTitle){
		  state.scrollTitle = scrollTitle;
	  }
  },
  actions: {
  },
  modules: {
  }
})
